
body,ul,ol,dl,dd,h1,h2,h3,h4,h5,h6,pre,form,fieldset,legend,input,button,textarea,p,blockquote,table,th,td,menu{margin:0;padding:0
}
table{border-collapse:collapse;border-spacing:0;table-layout:fixed
}
ul,ol,menu{list-style:none
}
fieldset,img{border:none
}
img,object,select,input,textarea,button{vertical-align:middle
}
input,textarea,select,address,caption,cite,code,dfn,em,i,b,strong,small,th,var,abbr{font-size:100%;font-style:normal
}
caption,th {text-align: left;
}
article,aside,footer,header,hgroup,nav,section,figure,figcaption {display: block;
}
code, kbd, pre, samp, tt { font-family: Consolas,"Courier New", Courier, monospace;
}
address, cite, dfn, em, var,i {font-style: normal;
}
blockquote, q {quotes: none;
}
blockquote:before, blockquote:after,q:before, q:after {content:"";content: none;
}
@font-face {
    font-family: 'iconfont';
    src: url(data:application/vnd.ms-fontobject;base64,);
    src: url(data:application/vnd.ms-fontobject;base64,?#iefix) format('embedded-opentype'),
    url(data:application/font-woff;base64,) format('woff'),
    url(data:application/x-font-ttf;base64,) format('truetype'),
    url(data:image/svg+xml;base64,#iconfont) format('svg');
}
.iconfont{
    font-family:"iconfont" !important;
    font-size:16px;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /*margin-top: 60px;*/
}
.vux-tabbar-simple .weui-tabbar__label {
  line-height: normal!important;
}
.weui-tabbar {
  position: fixed!important;
}
.weui-tabbar__label {
  margin: 0!important;
}
.weui-tabbar__item {
  text-decoration: none;
}

.xxx[data-v-eff00f70] {
  border: 1px solid;
  padding: 0 10px;
  margin-bottom: 50px;
}
