.weui-tabbar__icon > i {
  font-size: 24px;
  color: #999999;
}
.weui-tabbar__icon img {
  width: 100%;
  height: 100%;
}
.weui-tabbar__label {
  text-align: center;
  color: #999999;
  font-size: 10px;
  line-height: 1.8;
}
.weui-tab {
  position: relative;
  height: 100%;
}
.weui-tab__panel {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%;
  padding-bottom: 50px;
