}
.weui-tab__content {
  display: none;
}
.vux-reddot,
.vux-reddot-border,
.vux-reddot-s {
  position: relative;
}
.vux-reddot:after,
.vux-reddot-border:after,
.vux-reddot-s:after {
  content: '';
  position: absolute;
  display: block;
  width: 8px;
  height: 8px;
  background-color: #f74c31;
  border-radius: 5px;
  right: -3px;
  top: -3px;
